.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  &--group {
    padding: 10px;
    border-radius: 5px;
    margin: 0;
    background-color: #49505e;
  }

  &__textarea {
    width: 92%;
    padding: 5px 10px;
    font-size: 16px;
  }
}